import { useEffect, useState } from 'react';
import liff from '@line/liff';

export interface LiffProfile {
  userId: string;
  displayName: string;
  pictureUrl?: string;
  statusMessage?: string;
}

export function useLiff() {
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [profile, setProfile] = useState<LiffProfile | null>(null);
  const [isLiffReady, setIsLiffReady] = useState(false);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const initLiff = async () => {
      try {
        const liffId = process.env.NEXT_PUBLIC_LIFF_ID;

        if (!liffId) {
          setError('LIFF ID is not configured');
          setIsLiffReady(true); // エラーでもReadyにする
          return;
        }

        // すでに初期化済みかチェック
        try {
          // LIFF が既に初期化されている場合、isLoggedIn() は正常に動作する
          if (liff.isLoggedIn()) {
            // すでに初期化済み & ログイン済み - 即座に状態を取得
            setIsLiffReady(true);
            setIsLoggedIn(true);

            try {
              const userProfile = await liff.getProfile();
              setProfile({
                userId: userProfile.userId,
                displayName: userProfile.displayName,
                pictureUrl: userProfile.pictureUrl,
                statusMessage: userProfile.statusMessage,
              });
            } catch (profileErr) {
              console.error('Profile fetch failed:', profileErr);
            }
            return;
          } else {
            // すでに初期化済みだがログインしていない
            setIsLiffReady(true);
            return;
          }
        } catch {
          // LIFF が未初期化の場合、isLoggedIn() でエラーが発生するのでここで初期化を続行
        }

        // 初回初期化
        await liff.init({ liffId });
        setIsLiffReady(true);

        // ログイン状態を確認
        if (liff.isLoggedIn()) {
          setIsLoggedIn(true);

          // プロフィール情報を取得
          const userProfile = await liff.getProfile();
          setProfile({
            userId: userProfile.userId,
            displayName: userProfile.displayName,
            pictureUrl: userProfile.pictureUrl,
            statusMessage: userProfile.statusMessage,
          });
        }
      } catch (err) {
        console.error('LIFF initialization failed:', err);
        setError(err instanceof Error ? err.message : 'LIFF initialization failed');
        setIsLiffReady(true); // エラーでもReadyにする
      }
    };

    initLiff();
  }, []);

  const login = () => {
    if (!isLiffReady) {
      console.warn('LIFF is not ready yet');
      return;
    }
    liff.login();
  };

  const logout = () => {
    if (!isLiffReady) {
      console.warn('LIFF is not ready yet');
      return;
    }
    liff.logout();
    setIsLoggedIn(false);
    setProfile(null);
  };

  const sendMessage = async (message: string) => {
    if (!isLiffReady) {
      throw new Error('LIFF is not ready');
    }

    if (!liff.isInClient()) {
      throw new Error('This feature is only available in LINE app');
    }

    await liff.sendMessages([
      {
        type: 'text',
        text: message,
      },
    ]);
  };

  const closeWindow = () => {
    if (!isLiffReady) {
      console.warn('LIFF is not ready yet');
      return;
    }
    liff.closeWindow();
  };

  const shareTargetPicker = async (messages: any[]) => {
    if (!isLiffReady) {
      throw new Error('LIFF is not ready');
    }

    if (!liff.isApiAvailable('shareTargetPicker')) {
      throw new Error('ShareTargetPicker is not available');
    }

    try {
      const result = await liff.shareTargetPicker(messages);
      return result;
    } catch (err) {
      console.error('ShareTargetPicker error:', err);
      throw err;
    }
  };

  return {
    isLoggedIn,
    profile,
    isLiffReady,
    error,
    login,
    logout,
    sendMessage,
    closeWindow,
    shareTargetPicker,
    isInClient: isLiffReady ? liff.isInClient() : false,
  };
}
